.year_filter{
    display: flex;
    align-items: center;
    background: #d2d2d2;
    padding: 20px;
    z-index: 20;
    border-radius: 0px 0px 10px 10px;

}


.after{
    margin: 0px 5px;
    width: 12px;
    height: 2px;
    background: #000;
}

@media screen and (min-width:320px) and (max-width:1024px) {
    .year_filter {
        display: flex;
        gap: 5px;
        flex-direction: column;

    }
}